import React from "react";
import "../App.scss";

import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import HomeSlider from "../components/HomeSlider";
import EventsCalendar from "../components/EventsCalendar";

function HomePage() {
  return (
    <div className="bg-light">
      <Navbar />
      <HomeSlider />
      <div className="spacer"></div>
      <div className="custom-padding">
        <p className="mb-5 fw-bold" style={{ fontSize: "4rem" }}>
          ldinfo Tartu rakendusliku kooli kohta
        </p>
        <p style={{ fontSize: "3rem" }}>
          Tartu Rakenduslik Kolled on Eesti suurim kutse- ja tiendusppekeskus
          Eestis. Meie phitegevuseks on:
        </p>
        <ul style={{ fontSize: "2rem", paddingLeft: "5rem" }}>
          <li>
            pakkuda kutsepet phihariduse baasil ja keskhariduse baasil
            ppijatele;
          </li>
          <li>pakkuda kutsekeskhariduspet;</li>
          <li>
            pakkuda kutsepet koolikohustuse ea letanud phihariduseta
            isikutele;
          </li>
          <li>korraldada tkohaphist pet koosts tandjatega;</li>
          <li>
            viia lbi kutsevaliku programme, kutsealast eelkoolitust ja
            kutsepet phikoolis ja gmnaasiumis;
          </li>
          <li>korraldada tiskasvanute tienduskoolitust;</li>
          <li>korraldada karjrialast nustamist;</li>
          <li>pakkuda tooteid ja teenuseid Tartu linna elanikele.</li>
        </ul>
      </div>
      <div className="spacer"></div>
      <EventsCalendar />
      <Footer />
    </div>
  );
}
export default HomePage;
