import React, { createContext, useContext, useState } from "react";

const EventContext = createContext();

export const EventProvider = ({ children }) => {
  const [events, setEvents] = useState([
    {
      id: 1,
      group: "KIK",
      date: "2025-03-11",
      heading: "EMAKEELENDALVOCOS",
      description:
        "14. mrts valiti emakeelepevaks kui eesti esimese kirjaniku Kristjan Jaak Petersoni (1801-1822) snnipev. Kik, kes soovivad, saavad 14. mrtsil osa vtta! Lisaks toimub viktoriin kolmes astmes, mille auhinnaks on Valdur Mikita teos Kodukija koodeks.",
    },
    {
      id: 2,
      group: "KIK",
      date: "2025-03-20",
      heading: "AASTA TEGIJA 2025 KUTSEVISTLUSED ON PEETUD!",
      description:
        "Aasta Tegija 2025 tnugala ti VOCO kogukonna taas kokku! Aasta Tegija sndmus ei thista ksnes parimaid tulemusi, aga ka hist pingutust, oskuste vrtustamist ja koostd.",
    },
    {
      id: 3,
      group: "KIK",
      date: "2025-04-10",
      heading: "VOCO AVATUD UKSED 10. APRILLIL",
      description:
        'Meie VOCO "Avatud Uste Pev" toimub sel aastal 10. aprillil 2025, ning ootame taas rohkelt phikoolipilasi ja nende petajaid, et tutvustada meie kooli erialasid, vimalusi ja pikeskkonda.',
    },
    {
      id: 4,
      group: "KIK",
      date: "2025-03-21",
      heading: '"KAS SINA JUHID RAHA VI JUHIB RAHA SIND?"',
      description:
        "Ettevtluspetajad Maire Kask ja Mariin Alatsei kutsuvad osalema Eesti Pangaliidu veebikonverentsi hisvaatamisele!\nToimumiskoht: Pllu ppehoone, avatud ala A209.\nKellaaeg: 12.0014.30.",
    },
  ]);

  return (
    <EventContext.Provider value={{ events, setEvents }}>
      {children}
    </EventContext.Provider>
  );
};

export const useEvents = () => useContext(EventContext);
